.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 3rem 1rem;
  background-color: rgb(179, 179, 179);
}
.user-inputs {
  width: 81.938rem;
  height: 100%;
  background-color: #fff;
  padding: 2rem;
  font-family: var(--user-input-font-family);
}
.reservation {
  font-size: 1.5rem;
  font-weight: 700;
}
.print-btn {
  width: 10.938rem;
  height: 3rem;
  background: #5d5cff;
  border: none;
  outline: none;
  border-radius: 5px;
  color: #fff;
  font-weight: 600;
  font-size: 0.875rem;
}
.sub-header {
  font-weight: 600;
  font-size: 1.125rem;
}
hr {
  color: #5d5cff;
}
form {
  border: 1px solid #dfdfff;
  padding: 1.25rem;
  border-radius: 5px;
}
.form-label {
  font-size: 14px;
  font-weight: 300;
  color: #1b1b1b;
}
.form-check-label,
.charge,
td {
  color: #1b1b1b;
  font-size: 14px;
  font-weight: 400;
}
th {
  color: #1b1b1b;
  font-size: 14px;
  font-weight: 600;
}
.required {
  color: #ff1212;
  font-size: 14px;
  font-weight: 600;
}

.summery-container {
  border: 1px solid #5d5cff;
  padding: 1.25rem;
  border-radius: 5px;
  background-color: #dfdfff;
}
.opening{
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 14px;
}
.hidden {
  display: none;
}

@media print {
  /* Apply styles specifically for printing */

  body {
    font-family: Arial, sans-serif;
    font-size: 12px;
  }

  /* Hide elements not relevant for printing */
  .hidden {
    display: block;
  }

  /* Adjust margins for printing */
  .row {
    margin-bottom: 5mm; /* Adjust as needed */
  }

  /* Adjust font sizes, colors, etc. */
  .fw-bold {
    font-weight: bold;
  }

  .renters-data {
    margin-top: 10px;
  }

  .opening {
    list-style: none;
    padding: 0;
  }

  /* Adjust image size */
  img {
    max-width: 100%;
    height: auto;
  }


  /* Add any additional print-specific styles here */
}

